"use client"

import { useEffect, useState } from "react"
import { ArrowRight, Zap } from "lucide-react"

export default function Hero({ setActiveSection }) {
  const [displayText, setDisplayText] = useState("")
  const fullText = "Full-Stack Developer"
  const [textIndex, setTextIndex] = useState(0)

  useEffect(() => {
    if (textIndex < fullText.length) {
      const timer = setTimeout(() => {
        setDisplayText(fullText.slice(0, textIndex + 1))
        setTextIndex(textIndex + 1)
      }, 50)
      return () => clearTimeout(timer)
    }
  }, [textIndex])

  return (
    <section id="hero" className="relative min-h-screen flex items-center justify-center pt-20 px-4">
      {/* Animated background elements */}
      <div className="absolute top-20 left-10 w-72 h-72 bg-primary opacity-10 rounded-full blur-3xl animate-pulse" />
      <div className="absolute bottom-20 right-10 w-72 h-72 bg-accent opacity-10 rounded-full blur-3xl animate-pulse" />

      <div className="relative z-10 max-w-4xl mx-auto text-center">
        {/* Badge */}
        <div className="mb-8 inline-flex items-center gap-2 px-4 py-2 rounded-full glass">
          <Zap size={16} className="text-accent" />
          <span className="text-sm text-accent">Available for opportunities</span>
        </div>

        {/* Main Heading */}
        <h1 className="text-5xl md:text-7xl font-bold mb-6 leading-tight">
          <span className="gradient-text">{displayText}</span>
          <span className="animate-pulse">|</span>
        </h1>

        {/* Subtitle */}
        <p className="text-lg md:text-xl text-foreground/70 mb-8 max-w-2xl mx-auto leading-relaxed">
          I craft elegant solutions to complex problems. Specializing in modern web technologies, responsive design, and
          scalable architecture.
        </p>

        {/* CTA Buttons */}
        <div className="flex flex-col sm:flex-row gap-4 justify-center mb-16">
          <button
            onClick={() => {
              const aboutSection = document.getElementById("about")
              if (aboutSection) {
                aboutSection.scrollIntoView({ behavior: "smooth" })
              }
            }}
            className="px-8 py-3 bg-primary text-primary-foreground rounded-lg font-semibold flex items-center justify-center gap-2 hover:shadow-lg hover:shadow-primary/50 transition-all"
          >
            View My Work <ArrowRight size={20} />
          </button>
          <button
            onClick={() => {
              const contactSection = document.getElementById("contact")
              if (contactSection) {
                contactSection.scrollIntoView({ behavior: "smooth" })
              }
            }}
            className="px-8 py-3 border border-primary text-primary rounded-lg font-semibold hover:bg-primary/10 transition-all"
          >
            Get In Touch
          </button>
        </div>

        {/* Tech Stack Preview */}
        <div className="flex flex-wrap justify-center gap-3 text-sm">
          {["React", "Next.js", "TypeScript", "Tailwind"].map((tech) => (
            <div key={tech} className="px-3 py-1 rounded-full bg-secondary/50 text-foreground/70 border border-border">
              {tech}
            </div>
          ))}
        </div>
      </div>

      {/* Scroll indicator */}
      <div className="absolute bottom-8 left-1/2 transform -translate-x-1/2 animate-bounce">
        <div className="w-6 h-10 border-2 border-accent rounded-full flex justify-center">
          <span className="text-accent text-xs mt-1">â†“</span>
        </div>
      </div>
    </section>
  )
}
